export default {
    props: {
        label: {
            type: String,
            default: '标签'
        },
        value: {
            type: String,
            default: ''
        },
        checked: {
            type: Boolean,
            default: false
        },
        other: {
            type: [Object, Number, String, Array, Boolean],
            default: ''
        }
    }, 
    methods: {
        change(val) {
            this.$emit('change', {
                label: this.label,
                checked: val.detail.value,
                value:this.value,
                other:this.other
            })
        }
    },
}
